::selection {
    background-color: $clr1;
    color: $clr3;
}

* {
    box-sizing: border-box;
}

html { 
   line-height: lh(26px);
   height: 100%;

}

body {
    margin: 0;
    height: 100%;
}

#root {
    height: 100%;
}

h1, h2, h3, h4, h5, h6 {
    font-weight: 500;
    margin: 0 0 1rem;
    line-height: lh(40px, 29px);
}

h1 {
    font-size: rem(60px);
}

img {
    width: 100%;
}

p {
    margin: 0;
}

ul{
    margin: 0;
    padding: 0;
}

.btn {
    @include btn;
    
    &-dark {
        @include btn($type: dark);
    }
    
    &-light {
        @include btn($type: light);
    }
}


// links
a {
    color: $clr2;
    
    &.active {
        color: $clr1;
    }
}

