.game-board {
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    img.move-img {
        width: 50px;
    }

    header.game-header {
        margin: 20px auto;
        display: flex;
        align-items: center;
        justify-content:space-between;
        width: 40%;
        border: 4px solid grey;
        padding: 15px 15px 15px 20px;
        border-radius: 10px;
        @include for-normal-layout {
            width: 45%;
        }
        @include for-narrow-layout {
            width: 500px;
        }
        @include for-wide-mobile-layout {
            width: 450px;
        }
        @include for-small-mobile-layout {
            width: 360px;
        }
        .logo {
            background-image: url(../../assets/images/logo.svg);
            background-repeat: no-repeat;
            background-size: contain;
            background-position: center;
            height: 100px;
            width: 200px;
            @include for-small-mobile-layout {
                width: 100px;
            }
        }
        .score{
            display: flex;
            flex-direction: column  ;
            font-size: 1.5rem;
            background-color: #ffffff;
            padding: 10px 25px;
            border-radius: 8px;
            .player-score{
                font-size: 1.5em;
            }
            @include for-small-mobile-layout {
                font-size: unset;
            }
        }
    }
}
