.moves-list {
    background: #ffffff00 url(../../assets//images/bg-triangle.svg) no-repeat center;
    // Mobile 700px-
    @include for-wide-mobile-layout {
        // width: 80%;
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-template-rows: 1fr 1fr;
        justify-items: center;
        align-items: center;
        height: 40%;
    }
    @include for-narrow-layout {
        // width: 45%;
    }
    // width: 30%;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: 1fr 1fr;
    justify-items: center;
    align-items: center;
    height: 45%;
    .move-preview {
        @include for-normal-layout {
            width: 200px;
        }
        @include for-narrow-layout {
            // width: 150px;
            // height: 80%;
        }
        @include for-wide-mobile-layout {
            background-color: #ffffff;
            padding: 7.5px;
            width: 150px;
            // height: 60%;
            display: flex;
            justify-content: center;
            align-items: center;
            border-radius: 50%;
        }
        @include for-small-mobile-layout {
            width: 120px;
            height: 65%;
        }
        // Mobile 400px
        background-color: #ffffff;
        width: -moz-fit-content;
        width: fit-content;
        padding: 7.5px;
        width: 215px;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 50%;
        img.move-img {
            &:hover {
                cursor: pointer;
            }
        }
        &:first-child {
            grid-column: 1;
            transform: translateX(30%);
            border: 24px solid #306fe4;
            @include for-wide-mobile-layout {
                border: 18px solid #306fe4;
            }
            @include for-small-mobile-layout {
                border: 15px solid #306fe4;
                transform: unset;
                transform: translateY(-10%);
            }
        }
        &:nth-child(2) {
            grid-row: 2;
            grid-column: 2;
            border: 24px solid #fc5e4a;
            @include for-wide-mobile-layout {
                border: 18px solid #fc5e4a;
            }
            @include for-small-mobile-layout {
                border: 15px solid #fc5e4a;
                transform: unset;
                transform: translateY(-10%);
                transform: unset;
                transform: translateY(-10%);
            }
        }
        &:last-child {
            grid-column: 3;
            transform: translateX(-30%);
            border: 24px solid #ffbb1e;
            @include for-wide-mobile-layout {
                border: 18px solid #ffbb1e;
            }
            @include for-small-mobile-layout {
                border: 15px solid #ffbb1e;
                transform: unset;
                transform: translateY(-10%);
                transform: unset;
                transform: translateY(-10%);
            }
        }
    }
}
